

body {
  background-color: rgb(83, 81, 81);

    font-family: 'Josefin Sans',
    sans-serif;
}

.main{
  background-image: linear-gradient(to bottom, #30bec3, white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  padding: 5px 30px;
  border-radius: 50px;
  box-shadow: 0 2px 2px black;
  width: 380px;
  margin: 5px auto;
  padding-bottom: 20px;
}

.main h1{
  text-align: center;
}



.section1 {
  background-color: white;
  margin-top: 10px;
  padding: 20px 20px;
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;

  box-shadow: inset 0 2px 2px -2px grey;
  border-top: 3px solid blue;

}

.section2 {
  text-align: center;
  background-color: white;
  margin-top: 15px;
  padding: 40px 20px;
  border-radius: 10px;
  box-shadow: inset 0 2px 2px -2px gray;
  border-bottom: 3px solid blue;
  border-top: 3px solid blue;
  font-size: 14px;
  height:50px;
  overflow:scroll;
  overflow-x:hidden;
  overflow-y: auto;
}
::-webkit-scrollbar {
  width: 3px;

}
/* Track */
::-webkit-scrollbar-track {
  background:#f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: black;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: grey
}


ul{
  margin: 0;
  max-width: 400px;
}

.section3 {
  background-color: white;
  margin-top: 15px;
  padding: 20px 20px;
  border-bottom-left-radius: 40px;
  border-bottom-right-radius: 40px;
  box-shadow: inset 0 2px 2px -2px gray;
  border-bottom: 3px solid blue;
}


.section2>div {
  margin: 10px 0;
}

.section3>div {
  margin: 15px 0;
}

.bill {
  display: flex;
  font-weight: 600;
}

.bill label {
  flex: 1;
  align-self: center;
}

.bill input {
  flex: 1;

}

.addCustomer{
 display: flex;
 font-weight: 600;
 margin: 0 auto;
 margin-top: 30px;
 margin-bottom: 5px;
}


.addCustomer button {
  flex: 1;
  color: white;
  background-color: blue;
  margin-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 10px;
    outline: none;
    border: none;
    box-shadow: 0 2px 2px -2px black;
  cursor: pointer;
  font-family: 'Josefin Sans',
  sans-serif;
}

.addCustomer input {
  flex: 1;
}

.section3 button{
   color: white;
   background-color: blue;
   padding-top: 10px;
   padding-bottom: 10px;
   border-radius: 10px;
   outline: none;
   border: none;
   box-shadow: 0 2px 2px -2px black;
   width: 100%;
   cursor: pointer;
   transition: 0.2s;
   font-family: 'Josefin Sans',
   sans-serif;
}

.section3 button:hover,
.addCustomer button:hover{
transform: translateY(-2px);
}





.range label{
  display: flex;
  font-weight: 600;
  margin-top: 20px;
}
.range input{
  margin-top: 10px;
}


.space-between {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
 
}

#values{
  margin: 5px 40px;
  margin-left: 60px;
}

#totalname{
  margin: 0 20px;
  margin-top: 15px;
  
}

.section2 h4 {
  text-align: center;
  position: absolute;
  bottom:310px;
  left: 40%;
  font-weight: 600;
  
}
ul{
  
  position: relative;
  bottom: 10px;
  right: 20px;
}

.span{
  font-size: 13px;
  text-align: center;
  padding-top: 10px;
  
}

input[type="range"]{
  width: 100%;
}


input[type="number"] {
  background-color: black;
  color: white;
  padding: 10px;
  width: 30%;
  border-radius: 10px;
  outline: none;
  border: none;
  box-shadow: inset 0 2px 2px -2px black;
  font-family: 'Mali',
    cursive;
}

input[type="text"] {
  background-color: black;
  color: white;
  border-radius: 10px;
  padding: 10px;
  width: 30%;
  outline: none;
  border: none;
  box-shadow: inset 0 2px 2px -2px black;
  font-family: 'Mali',
    cursive;
}


@media (min-width:450px) {
  .main {
    width: 430px;
  }
}
